import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { deleteUserAccount } from './deleteAccountSlice';
import {
  DeleteAccountContainer,
  DeleteAccountTitle,
  DeleteAccountBox,
  Label,
  Input,
  Button,
} from './style';

const DeleteAccount = () => {
  const [password, setPassword] = useState('');
  const [confirmationText, setConfirmationText] = useState('');
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // 
  const handleAccountDelete = (e) => {
    e.preventDefault();

    // 
    if (confirmationText !== '') {
      dispatch({ type: 'deleteUser/setError', payload: ' "" ' });
      return;
    }

    // 
    dispatch(deleteUserAccount({ password, confirmationText }))
      .then((result) => {
        if (result.meta && result.meta.requestStatus === 'fulfilled') {
          window.alert('');
          navigate('/');
        }
      })
      .catch((err) => {
        const errorMessage = err.response?.status === 403 
          ? err.response.data.error 
          : '';
        window.alert(errorMessage);
      })
      .finally(() => {
          setPassword('');
          setConfirmationText('');
          dispatch({ type: 'deleteUser/clearError' }); 
        }, 3000);
  };

  // 
  useEffect(() => {
    return () => {
      setPassword('');
      setConfirmationText('');
      dispatch({ type: 'deleteUser/clearError' });
    };
  }, [dispatch]);

  return (
    <DeleteAccountContainer>
      <DeleteAccountTitle></DeleteAccountTitle>
      <DeleteAccountBox>
        <form onSubmit={handleAccountDelete}>
          <Label htmlFor="password"></Label>
          <Input
            id="password"
            type="password"
            placeholder="Password"
            value={password}
            maxLength={20}
            onChange={(e) => setPassword(e.target.value)}
            autoComplete="current-password"
            required
          />

          <Label htmlFor="confirmationText"> "" </Label>
          <Input
            id="confirmationText"
            type="text"
            placeholder=""
            value={confirmationText}
            maxLength={20}
            onChange={(e) => setConfirmationText(e.target.value)}
            required
          />
          <Button type="submit" disabled={password === '' || confirmationText !== ''}>
            
          </Button>
        </form>
      </DeleteAccountBox>
    </DeleteAccountContainer>
  );
};

export default DeleteAccount;