import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { sendResetEmail, setEmail, setEmailValid, setEmailError, setError, resetForm } from '../../forgotPasswordSlice';
import {
  validateEmail
} from '../../../../../../utils/validation';
import {
  ForgotPasswordContainer,
  ForgotPasswordTitle,
  ForgotPasswordBox,
  Label,
  Input,
  Button,
  ErrorText
} from '../../style';

const EmailInput = () => {
  const dispatch = useDispatch();
  const { email, emailError, emailValid, error, loading } = useSelector((state) => state.forgotPassword);

  // 
  const handleEmailChange = (e) => {
    const inputEmail = e.target.value;
    dispatch(setEmail(inputEmail));
  
    // 
    const isValid = validateEmail(inputEmail);
    dispatch(setEmailValid(isValid));
  
    // 
    if (isValid || inputEmail === '') {
      dispatch(setEmailError(''));
      dispatch(setError(''));
    } else {
      dispatch(setEmailError(''));
    }
  };  

  // 
  const handleSendResetEmail = async (e) => {
    e.preventDefault();

    // 
    if (!emailValid) {
      window.alert('');
      return;
    }

    try {
      // 
      await dispatch(sendResetEmail(email)).unwrap();
      window.alert('');
    } catch (err) {
      window.alert('');
      dispatch(setError(err.message || ''));
    } finally {
      // 
      dispatch(resetForm());
    }
  };

  return (
    <ForgotPasswordContainer>
      <ForgotPasswordTitle></ForgotPasswordTitle>
      <ForgotPasswordBox>
        <form onSubmit={handleSendResetEmail}>
          <Label></Label>
          <Input 
            type="email" 
            placeholder="Email" 
            value={email}
            maxLength={50}
            onChange={handleEmailChange} 
            required 
          />
          {emailError && <ErrorText>{emailError}</ErrorText>}
          {error && <ErrorText>{error}</ErrorText>}
          <Button type="submit" disabled={loading}></Button>
        </form>
      </ForgotPasswordBox>
    </ForgotPasswordContainer>
  );
};

export default EmailInput;