import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { resetPassword, setPasswordValid, setPasswordError, setConfirmPasswordValid, setConfirmPasswordError } from '../../forgotPasswordSlice';
import {
  validatePassword,
  validateConfirmPassword
} from '../../../../../../utils/validation';
import {
  ForgotPasswordContainer,
  ForgotPasswordTitle,
  ForgotPasswordBox,
  Label,
  Input,
  Button,
  ErrorText,
  SuccessText,
  HelperText
} from '../../style';

const ResetPassword = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { passwordValid, confirmPasswordValid, passwordError, confirmPasswordError, loading } = useSelector((state) => state.forgotPassword);

  // 
  const [formData, setFormData] = useState({
    password: '',
    confirmPassword: ''
  });
  const [error, setError] = useState('');

  //  oobCode  apiKey
  useEffect(() => {
    const hashParams = new URLSearchParams(window.location.hash.split('?')[1]);
    const oobCode = hashParams.get('oobCode');
    const apiKey = hashParams.get('apiKey');

    //  oobCode  apiKey 
    if (!oobCode || !apiKey) {
      navigate('/');
    }
  }, [navigate]);

  // 
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // 
  const handlePasswordValidation = (password) => {
    const isValid = validatePassword(password);
    dispatch(setPasswordValid(isValid));
    dispatch(setPasswordError(isValid ? '' : ''));
  };

  // 
  const handleConfirmPasswordValidation = (password, confirmPassword) => {
    const isValid = validateConfirmPassword(password, confirmPassword);
    dispatch(setConfirmPasswordValid(isValid));
    dispatch(setConfirmPasswordError(isValid ? '' : ''));
  };

  // 
  const handlePasswordChange = async (e) => {
    e.preventDefault();
    const { password } = formData;

    // 
    if (!passwordValid) {
      window.alert('');
      return;
    }

    // 
    if (!confirmPasswordValid) {
      window.alert('');
      return;
    }

    //  URL  hash  oobCode
    const hashParams = new URLSearchParams(window.location.hash.split('?')[1]);
    const oobCode = hashParams.get('oobCode');

    try {
      // 
      await dispatch(resetPassword({ oobCode, password })).unwrap();
      window.alert('');
      navigate('/login');
    } catch (err) {
      console.error(':', err);
      setError(err.message || '');
    }
  };

  return (
    <ForgotPasswordContainer>
      <ForgotPasswordTitle></ForgotPasswordTitle>
      <ForgotPasswordBox>
        <form onSubmit={handlePasswordChange}>
          <Label>:</Label>
          <Input
            type="password" 
            name="password"
            placeholder="" 
            maxLength={20}
            value={formData.password} 
            onChange={(e) => {
              handleChange(e);
              handlePasswordValidation(e.target.value);
            }}
            required 
          />
          {formData.password &&
            (passwordValid ? (
              <SuccessText></SuccessText>
            ) : (
              <ErrorText> {passwordError}</ErrorText>
            ))}
          <HelperText>8</HelperText>
          <Label>:</Label>
          <Input 
            type="password" 
            name="confirmPassword"
            placeholder=""
            maxLength={20} 
            value={formData.confirmPassword} 
            onChange={(e) => {
              handleChange(e);
              handleConfirmPasswordValidation(formData.password, e.target.value);
            }}
            required 
          />
          {formData.confirmPassword &&
            (confirmPasswordValid ? (
              <SuccessText></SuccessText>
            ) : (
              <ErrorText> {confirmPasswordError}</ErrorText>
            ))}
          {error && <ErrorText>{error}</ErrorText>}
          <Button type="submit" disabled={loading}></Button>
        </form>
      </ForgotPasswordBox>
    </ForgotPasswordContainer>
  );
};

export default ResetPassword;